import Vue from 'vue'
import Vuex from 'vuex'
import { State } from './state'

import { getters } from './getters'
import { mutations } from './mutations'

declare namespace window {
  export const Vue: any
}

if (typeof window === 'undefined' || !window.Vue) {
  // [vuex] already installed. Vue.use(Vuex) should be called only once.
  //   Vuex 
  Vue.use(Vuex)
}

const state: State = {
  key: 'APLAYER_VEUX_STATE',
  audio: new Audio(),
  media: {
    audioTracks: null,
    /**  */
    autoplay: false,
    /**  TimeRanges  */
    buffered: null,
    /**  MediaController  */
    controller: null,
    /** / */
    controls: false,
    /**  CORS  */
    crossOrigin: null,
    /**  URL */
    currentSrc: null,
    /**  */
    currentTime: 0,
    /**  */
    defaultMuted: false,
    /**  */
    defaultPlaybackRate: 1,
    /**  */
    duration: 0,
    /**  */
    ended: false,
    /**  MediaError  */
    error: null,
    /**  */
    loop: false,
    /**  */
    mediaGroup: null,
    /**  */
    muted: false,
    /**  */
    networkState: null,
    /**  */
    paused: true,
    /**  */
    playbackRate: 1,
    /**  TimeRanges  */
    played: null,
    /**  preload  */
    preload: 'auto',
    /**  */
    readyState: null,
    /**  TimeRanges  */
    seekable: null,
    /**  */
    seeking: false,
    /**  src  */
    src: null,
    /**  TextTrackList  */
    textTracks: null,
    /**  */
    volume: 1
  },
  music: null,
  mode: 'circulation',
  theme: '#ad7a86',
  volume: 1,
  collapsed: false
}

state.audio.volume = 0.8
state.volume = state.audio.volume

export const store = new Vuex.Store<State>({ state, getters, mutations })
export default store



